.nextBtn {
  background: #ed1c24;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  border-radius: 0;
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;
  padding: 6px 25px;
  transition: transform 80ms ease-in;
  border: 1px solid #ed1c24;
}

.nextBtn:hover {
  background: #ed1c24;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  border-radius: 0;
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;
  padding: 6px 25px;
  transition: transform 80ms ease-in;
  border: 1px solid #ed1c24;
}
